@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500;700&family=Poppins:wght@200;300;400;500;600;700&display=swap');
:root{
    --bg-black-900:#f2f2fc;
    --bg-black-100:#fdf9ff;
    --bg-black-50:#e8dfec;
    --text-black-900:#302e4d;
    --text-black-700:#504e70;
    --skin-color:#ec1839;

}
body{
    line-height: 1.5;
    font-family:'Poppins' sans-serif;
    background:#000000e3

}
*
{
    margin: 0;
    padding: 0;
    outline: none;
    text-decoration: none;
    box-sizing: border-box;
}
::after,::before{
    box-sizing: border-box;
}
ul{
    list-style: none;
}
.pad-15{
    padding-left: 15px;
    padding-right: 15px;
}
.container{
    max-width: 1024px;
    position: absolute;
    top: -6rem;
    margin-left:300px;
    position:absolute;
}
.row{
    display: flex;
    flex-wrap: wrap;
    margin-right:4rem;
    position: relative;
    margin-bottom: 5rem;
}
.shadow-dark{
    box-shadow: 0 0 20px rgba(233, 232, 237, 0.59);
}

.section{
    /* width:1346px; */ 
    padding: 0px;
    right: 0;
    z-index: 0;
    opacity: 1;
}



/* Aside css start */

.pad-15{
    padding-left: 15px;
    padding-right: 15px;
}


.aside{
    width: 230px;
    background: var(--bg-black-900);
    position: fixed;
    display: flex;
    left: 0;
    top: 0;
    padding: 10px;
    height: 100%;
    justify-content: center;
    align-items: center;
}
.nav{
    position: fixed;
   flex-direction: column;
}
.aside .logo{
    position: absolute;
    top: 30px;
    font-size: 30px;
    text-transform: capitalize;
}
.aside .logo a{
    color: var(--text-black-900);
    font-weight: 700;
    padding: 12px 13px;
    font-size: 17px;
    letter-spacing: 5px;
    font-style:italic;
    position: relative;
}
.aside .logo a span{
    font-family: 'Clicker Script',cursive;

}
.aside .logo a::before{
    content: '';
    position:absolute;
    width: 20px;
    min-height: 20px;
    border-top: 4px solid var(--skin-color);
    border-left: 4px solid var(--skin-color);
    top: 0;
    left: 0;

}
.aside .logo a::after{
    content: '';
    position:absolute;
    width: 20px;
    min-height: 20px;
    border-bottom: 4px solid var(--skin-color);
    border-right: 4px solid var(--skin-color);
    bottom: 1px;
    right:-0.1rem;
}
.aside .nav-toogler {
    height: 40px;
    min-width: 45px;
    border: 1px solid var(--bg-black-50);
    cursor: pointer;
    position: fixed;
    left: 300px;
    top: 20px;
    border-radius:5px;
    background: var(--bg-black-100);
    display: none;
    align-items: center;
    justify-content: center;
}
.aside .nav-toogler span{
    height: 2px;
    width: 18px;
    background: var(--skin-color);
    display:inline-block;
    position:relative;

}
.aside .nav-toogler span::before{
    content: '';
    min-height: 2px;
    width: 18px;
    background: var(--skin-color);
    position:absolute;
    top: -6px;
    left: 0;

}
.aside .nav-toogler span::after{
    content: '';
    min-height: 2px;
    min-width: 18px;
    background: var(--skin-color);
    position:absolute;
    top: 6px;
    left: 0;
}
.aside .nav li{
    margin-bottom: 20px;
    display: block;
}
.aside .nav li a {
    font-size: 25px;
    font-weight: 400;
    display: block ;
    border-bottom: 1px solid var(--bg-black-50);
    color: var(--text-black-900);
    padding: 5px 15px;

}
.aside .nav li a.active{
    color: var(--skin-color);
}
.aside .nav li a i{
    margin-right: 10px;
}




.portfolio-title{
    margin-top:10rem;
    color: var(--bg-black-100);
}
.portfolio-title::after{
    content: '';
    height: 4px;
    width: 80px;
    background-color: var(--skin-color);
    position: absolute;
    left: 0;
}
.portfolio-title::before{
    content: '';
    height: 4px;
    width: 60px;
    background-color: var(--skin-color);
    position: absolute;
    top:13.7rem;
    left: 0;
}
.portfolio-heading{
    margin:4rem 0 4rem 0;
    color: var(--bg-black-100);
    font-size: 1.5rem;
    font-weight: 500;
}
.portfolio-item-container{
    min-width: 1200px;
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
}
.portfolio-item{
    flex: 0 0 30%;
    margin-bottom: 30px;
    border: 6px solid #00000000;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
}
.portfolio-img img{
    width: 100%;
}




/* Responsive Start */


@media (max-width:930px)
{
.aside{
    display: none;
}
.container{
     margin-left:30px;
     position: absolute;
}



}